.login {
  width: 100vw;
  height: 100vh;
}
@-webkit-keyframes stretchdelay {
  0% {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transform-origin: 0% 0%;
            transform-origin: 0% 0%;
  }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}
@keyframes stretchdelay {
  0% {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transform-origin: 0% 0%;
            transform-origin: 0% 0%;
  }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}
.login {
  min-height: 340px;
  min-width: 340px;
  background: url('/assets/images/bg.jpg') no-repeat;
  background-size: 100% 100%;
}
.login .mallmanasys-info {
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.3);
}
.login .mallmanasys-info h1 {
  color: rgba(255, 255, 255, 0.85);
  font-size: 35px;
  font-weight: 100;
  padding: 10px 30px;
  margin-bottom: 0px;
  -webkit-animation: stretchdelay 500ms ease-in;
          animation: stretchdelay 500ms ease-in;
}
.login .mallmanasys-info .mallmanasys-alias,
.login .mallmanasys-info .mallmanasys-author {
  color: rgba(255, 255, 255, 0.85);
  padding: 5px 30px;
  margin-bottom: 0px;
}
.login .mallmanasys-info .mallmanasys-alias {
  -webkit-animation: stretchdelay 500ms ease-in;
          animation: stretchdelay 500ms ease-in;
}
.login .mallmanasys-info .mallmanasys-author {
  padding-top: 0px;
  -webkit-animation: stretchdelay 500ms ease-in;
          animation: stretchdelay 500ms ease-in;
}
.login .login-form {
  max-width: 300px;
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.login .login-form .login-form-button {
  width: 100%;
  background-color: #158bc6;
}
.login .login-form .login-form-button:hover {
  background-color: #1278ab;
}
.login .login-form .login-btn {
  margin-bottom: 0px;
}
@media screen and (min-width: 320px) and (max-width: 768px) {
  .login .mallmanasys-info {
    text-align: center;
    margin-top: 40px;
  }
}
@media screen and (min-width: 768px) {
  .login .mallmanasys-info {
    position: absolute;
    bottom: 10%;
    right: 3%;
    text-align: right;
  }
  .login .mallmanasys-info h1 {
    background: rgba(0, 0, 0, 0.7);
  }
  .login .mallmanasys-info .mallmanasys-alias,
  .login .mallmanasys-info .mallmanasys-author {
    background: rgba(0, 0, 0, 0.3);
  }
  .login .login-form {
    background-color: rgba(0, 0, 0, 0.3);
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 20px 20px;
  }
}
