.goods-showtitle {
  font-size: 18px;
  font-weight: bolder;
  margin-bottom: 0px;
}
.goods-showprice {
  width: 80%;
  background: url('/assets/images/goodsinfo-price.png') no-repeat;
  background-size: 100% 100%;
  color: #fff;
  font-size: 18px;
  height: 40px;
  line-height: 40px;
}
.productDetailImg img {
  width: 150px;
  height: 120px;
}
.detail video {
