.mall-layout {
  height: 100vh;
}
@media screen and (min-width: 320px) and (max-width: 768px) {
  .mall-layout {
    min-width: 1200px;
  }
  .mall-layout .sider .ant-layout-sider-trigger {
    position: absolute;
    bottom: 0;
  }
  .mall-layout .ant-popover {
    width: 200px;
  }
}
.mall-layout .logo {
  text-align: center;
}
.mall-layout .sider {
  position: relative;
  overflow: auto;
  top: 0px;
  left: 0px;
  bottom: 0px;
  z-index: 1;
}
.mall-layout .content-layout .content-body {
  margin-top: 30px;
}
.mall-layout .footer {
  text-align: center;
  font-size: 14px;
}
