.home p {
  color: #fff;
  margin-bottom: 0;
}
.home .grid {
  cursor: pointer;
}
.home .grid:nth-child(1) {
  background-color: #f0b45f;
}
.home .grid:nth-child(1):hover {
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
  background-color: #f59d23;
}
.home .grid:nth-child(1):hover p {
  color: #000;
}
.home .grid:nth-child(2) {
  background-color: #6bbe6b;
}
.home .grid:nth-child(2):hover {
  background-color: #65d865;
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
}
.home .grid:nth-child(2):hover p {
  color: #000;
}
.home .grid:nth-child(3) {
  background-color: #5db8d3;
}
.home .grid:nth-child(3):hover {
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
  background-color: #47caf1;
}
.home .grid:nth-child(3):hover p {
  color: #000;
}
@media screen and (min-width: 320px) and (max-width: 768px) {
  .home .count {
    font-size: 18px;
  }
  .home .desc {
    font-size: 14px;
  }
}
@media screen and (min-width: 768px) {
  .home .count {
    font-size: 50px;
  }
  .home .desc {
    font-size: 18px;
  }
}
